import React, { Component } from "react";
import { Form, Button, Container, Row } from "react-bootstrap";
import logobox1 from "../images/box11.png";
import { Link } from "react-router-dom";
import { faSpinner } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

class Signup extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: null,
      name: null,
      rstate: null,
      address: null,
      password: null,
      cpassword: null,
      verify: null,
      otp: null,
      loading: false,
      loader: false,
      step: "",
      formErrors: {
        username: "",
        name: "",
        rstate: "",
        address: "",
        password: "",
        cpassword: "",
        verify: "",
      },
    };
  }
  handleSubmit(e) {
    e.preventDefault();
  }
  handleVerify(e) {
    e.preventDefault();
  }

  handleChange = (e) => {
    e.preventDefault();
    const { name, value } = e.target;
    let formErrors = this.state.formErrors;

    switch (name) {
      case "username":
        formErrors.username =
          value.length !== 11 && value.length > 0
            ? "Numbers only Minimum of 11"
            : "";
        break;
      case "name":
        formErrors.name =
          value.length < 4 && value.length > 0
            ? "Please enter a valid name"
            : "";
        break;
      case "rstate":
        formErrors.rstate =
          value.length < 4 && value.length > 0
            ? "Minimum 4 characters Required"
            : "";
        break;
      case "address":
        formErrors.address =
          value.length < 3 && value.length > 0
            ? "Please enter a valid address e.g No 20 Danube Street, Ibrahim Babangida, Maitama Abuja"
            : "";
        break;
      case "password":
        formErrors.password =
          value.length < 8 && value.length > 0
            ? "Minimum 8 characters Required"
            : "";
        break;
      case "cpassword":
        formErrors.cpassword =
          value === this.state.password
            ? ""
            : "Password and Confirm Password must match";
        break;
      case "verify":
        formErrors.verify =
          value === this.state.otp ? "Minimum 6 characters Required" : "";
        break;
      default:
        break;
    }

    this.setState({ formErrors, [name]: value }, () => console.log(this.state));
  };
  async verifyAccount() {
    if (String(this.state.otp) === this.state.verify) {
      try {
        this.setState({ loader: true });
        setTimeout(() => {
          this.setState({ loader: true });
        }, 3000);
        let res = await fetch("/signup", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            username: this.state.username,
            name: this.state.name,
            state: this.state.rstate,
            address: this.state.address,
            password: this.state.password,
          }),
        });

        let result = await res.json();
        if (result && result.success) {
          this.setState({
            step: "verified",
          });
          return;
        } else if (result && result.success === false) {
          alert(result.msg);
        }
      } catch (e) {
        console.log(e);
      }
      this.setState({
        step: "verified",
      });
    } else {
      alert("invalid otp");
    }
  }
  async createAccount() {
    if (
      this.state.formErrors.username === "" &&
      this.state.formErrors.name === "" &&
      this.state.formErrors.rstate === "" &&
      this.state.formErrors.address === "" &&
      this.state.formErrors.password === "" &&
      this.state.cpassword !== "" &&
      this.state.username !== null &&
      this.state.name !== null &&
      this.state.rstate !== null &&
      this.state.address !== null &&
      this.state.password !== null &&
      this.state.cpassword === this.state.password
    ) {
      try {
        this.setState({ loading: true });
        setTimeout(() => {
          this.setState({ loading: true });
        }, 3000);
        this.state.otp = Math.floor(100000 + Math.random() * 900000);
        let res = await fetch("/sendmessage", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            username: this.state.username,
            otp: this.state.otp,
          }),
        });

        let result = await res.json();
        if (result && result.success) {
          this.setState({
            step: "process",
          });
        } else if (result && result.success === false) {
          alert(result.msg);
        }
      } catch (e) {
        console.log(e);
      }
    } else {
      alert("Please Fill out your form Correctly");
    }
  }

  toggleContent() {
    switch (this.state.step) {
      case "":
        const { formErrors } = this.state;
        const { loading } = this.state;
        return (
          <>
            <h5
              style={{
                fontSize: "1.625rem",
                lineHeight: " 2.25rem",
                fontWeight: "700",
                textAlign: "left",
                fontFamily: "Roboto, sans-serif",
              }}
            >
              Let's get you started with your Box1 account
            </h5>

            <Form onSubmit={this.handleSubmit}>
              <p style={{ position: "relative", textAlign: "center" }}></p>
              <Form.Group controlId="formBasicEmail">
                <Form.Label ClassName="Label-Form-Input">
                  Phone Number
                </Form.Label>
                <Form.Control
                  className="form-input"
                  name="username"
                  onChange={this.handleChange}
                  type="tel"
                  id="phone"
                  pattern="[0-9]{11}"
                  placeholder="0813 666 8344"
                  required
                />
                {formErrors.username.length > 0 && (
                  <span className="errorMessage">{formErrors.username}</span>
                )}
              </Form.Group>

              <Form.Group controlId="formBasicEmail">
                <Form.Label>Name</Form.Label>
                <Form.Control
                  className="form-input"
                  name="name"
                  onChange={this.handleChange}
                  type="text"
                  placeholder="Hussaini Bulama"
                  required
                />
                {formErrors.name.length > 0 && (
                  <span className="errorMessage">{formErrors.name}</span>
                )}
              </Form.Group>

              <Form.Group controlId="exampleForm.ControlSelect1">
                <Form.Label>State of Residence</Form.Label>
                <Form.Control
                  className="form-input"
                  name="rstate"
                  onChange={this.handleChange}
                  as="select"
                  required
                >
                  <option></option>
                  <option>Abuja</option>
                  <option>Abekuta</option>
                  <option>Abia</option>
                  <option>Adamawa</option>
                  <option>Bauchi</option>
                  <option>Borno</option>
                  <option>Enugu</option>
                  <option>Gombe</option>
                  <option>Ilorin</option>
                  <option>Kano</option>
                  <option>kaduna</option>
                  <option>katsina</option>
                  <option>Lagos</option>
                  <option>Onitsha</option>
                  <option>Sokoto</option>
                  <option>Yobe</option>
                </Form.Control>
                {formErrors.rstate.length > 0 && (
                  <span className="errorMessage">{formErrors.rstate}</span>
                )}
              </Form.Group>
              <Form.Group controlId="exampleForm.ControlTextarea1">
                <Form.Label>Residential Address</Form.Label>
                <Form.Control
                  className="form-input-text"
                  name="address"
                  onChange={this.handleChange}
                  as="textarea"
                  rows="3"
                  required
                />
                {formErrors.address.length > 0 && (
                  <span className="errorMessage">{formErrors.address}</span>
                )}
              </Form.Group>

              <Form.Group controlId="formBasicPassword">
                <Form.Label>Choose a Password</Form.Label>{" "}
                <Form.Control
                  className="form-input"
                  name="password"
                  onChange={this.handleChange}
                  type="password"
                  placeholder="Password"
                  required
                />
                {formErrors.password.length > 0 && (
                  <span className="errorMessage">{formErrors.password}</span>
                )}
              </Form.Group>
              <Form.Group controlId="formBasicPassword">
                <Form.Label>Confirm Password</Form.Label>{" "}
                <Form.Control
                  className="form-input"
                  name="cpassword"
                  onChange={this.handleChange}
                  type="password"
                  placeholder="Password"
                  required
                />
                {formErrors.cpassword.length > 0 && (
                  <span className="errorMessage">{formErrors.cpassword}</span>
                )}
              </Form.Group>
              <Button
                onClick={() => this.createAccount()}
                disabled={loading}
                variant="primary"
                type="submit"
                style={{ width: "100%", padding: "0.8em" }}
              >
                {loading && (
                  <FontAwesomeIcon
                    onClick={this.continue}
                    icon={faSpinner}
                    spin
                  />
                )}
                {loading && <span> Sending OTP</span>}

                {!loading && <span>Create an Account</span>}
              </Button>
              <br />

              <br />

              <p
                style={{
                  fontSize: "1.1rem",
                  fontFamily: "Nunito, sans-serif",
                }}
              >
                {" "}
                Already have an Account{" "}
                <Link
                  className="link"
                  to="/login"
                  style={{
                    position: "relative",

                    color: "#800000",
                    fontWeight: "600",
                    cursor: "pointer",
                  }}
                >
                  Login
                </Link>
              </p>
            </Form>
          </>
        );
        break;
      case "process":
        const { loader } = this.state;
        return (
          <>
            <Form onSubmit={this.handleVerify}>
              <h5
                style={{
                  fontSize: "1.625rem",
                  lineHeight: " 2.25rem",
                  fontWeight: "700",
                  textAlign: "left",
                  fontFamily: "Roboto, sans-serif",
                }}
              >
                Please enter the OTP sent to you
              </h5>
              {this.state.code}
              <Form.Group controlId="formBasicPassword">
                <Form.Label>Verify your number</Form.Label>{" "}
                <Form.Control
                  className="form-input"
                  name="verify"
                  onChange={this.handleChange}
                  type="text"
                  placeholder="verify"
                  required
                />
              </Form.Group>

              <Button
                onClick={() => this.verifyAccount()}
                variant="primary"
                type="submit"
                disabled={loader}
                style={{ width: "100%", padding: "0.8em" }}
              >
                {loader && (
                  <FontAwesomeIcon
                    onClick={this.continue}
                    icon={faSpinner}
                    spin
                  />
                )}
                {loader && <span> Verifying OTP sent</span>}

                {!loader && <span>Verify</span>}
              </Button>
            </Form>
          </>
        );
        break;
      case "verified":
        return (
          <>
            <div
              style={{
                backgroundColor: "#fff",
                padding: "2em",
                borderRadius: "2em",
              }}
            >
              <h3 style={{ fontSize: "17px", textAlign: "justify" }}>
                Congratulations, your account has been created successfully, you
                may proceed to login now. <Link to="/login">Login now</Link>
              </h3>
            </div>
          </>
        );
        break;
      default:
        break;
    }
  }
  render() {
    return (
      <>
        <div className="remove-grid">
          <Container>
            <Row>
              <div className="signup-form">
                <div
                  style={{
                    position: "relative",

                    justifyContent: "center",
                  }}
                >
                  <Link className="link" to="/">
                    <div style={{ float: "center", textAlign: "center" }}>
                      <img
                        className="logo"
                        src={logobox1}
                        alt="logo"
                        style={{
                          marginLeft: "0",
                          left: "0",
                          textAlign: "center",
                          justifyContent: "center",
                        }}
                      />
                    </div>
                  </Link>
                  <br />
                  <br />
                  <>{this.toggleContent()}</>
                </div>
              </div>
            </Row>
          </Container>
        </div>
      </>
    );
  }
}
export default Signup;
