import React, { Component } from "react";
import { faHistory } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";

import UserStore from "../../../UserStore";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";

const Styles = makeStyles({
  table: {
    minWidth: "250 !important",
  },
});

class MyShipments extends Component {
  async componentDidMount() {
    window.addEventListener("resize", this.resize);
    try {
      let res = await fetch("/isLoggedIn", {
        method: "post",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });

      let result = await res.json();
      if (result && result.success) {
        UserStore.loading = false;
        UserStore.isLoggedIn = true;
        UserStore.username = result.username;
      } else {
        UserStore.loading = false;
        UserStore.isLoggedIn = false;
      }
    } catch (e) {
      UserStore.loading = false;
      UserStore.isLoggedIn = false;
    }
    try {
      let res = await fetch("/fetchhistory", {
        method: "post",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          sessionID: UserStore.sessionID,
        }),
      });
      const results = await res.json();
      this.setState({ results });
    } catch (e) {
      console.log(e);
    }
  }
  constructor(props) {
    super(props);
    this.state = {
      results: [],
    };
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.resize);
  }

  resize = () => this.forceUpdate();

  render() {
    return (
      <>
        <div
          style={{
            backgroundColor: "#fff",
            padding: "2em",
            borderRadius: "0.5em",
            width: "100% !important",
            margin: "0 auto",
          }}
        >
          <div className="demo-tabs">
            <div style={{ textAlign: "center" }}>
              <FontAwesomeIcon
                icon={faHistory}
                style={{
                  display: "inline-block",
                  color: "#800000",
                  fontSize: "30px",
                }}
              />
              <p
                style={{
                  fontFamily: "Roboto, sans-serif",
                  color: "#00000",
                  fontSize: "20px",
                }}
              >
                Your Shipment(s) History
              </p>

              <hr />
            </div>

            <div className="Play-Ground">
              <section>
                <div
                  className="jss53 jss55"
                  style={{ width: "30% !important" }}
                >
                  <TableContainer component={Paper}>
                    <Table className={Styles.table} aria-label="simple table">
                      <TableHead>
                        <TableRow>
                          <TableCell>Item Type</TableCell>
                          <TableCell align="right">
                            Reciever`s Details
                          </TableCell>
                          <TableCell align="right">Date&nbsp;</TableCell>
                          <TableCell align="right">Status&nbsp;</TableCell>
                        </TableRow>
                      </TableHead>
                      <TableBody>
                        {this.state.results.map((result) => (
                          <TableRow key={result.itemtype}>
                            <TableCell component="th" scope="row">
                              {result.itemtype}
                            </TableCell>
                            <TableCell align="right">
                              {result.rname}-{result.rphone}-{result.dlocation}
                            </TableCell>
                            <TableCell align="right">
                              {result.darchieved}
                            </TableCell>
                            <TableCell align="right">Delivered</TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </TableContainer>
                </div>
              </section>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default MyShipments;
