import React from "react";
import { Switch, Route } from "react-router-dom";

import Home from "./home";
import About from "./about";
import Login from "./login";
import Signup from "./signup";
import Dashboard from "./dashboard/dashboard";
import Subscription from "./dashboard/content/Subscription";
import PrepareShipment from "./dashboard/content/PrepareShipment";
import ScheduleShipment from "./dashboard/content/ScheduleShipment";
import Settings from "./dashboard/content/settings";
import MyShipments from "./dashboard/content/myshipments";
import CreditCard from "./dashboard/content/CreditCard";

const Main = () => (
  <Switch>
    <Route exact path="/" component={Home} />
    <Route exact path="/about" component={About} />
    <Route exact path="/login" component={Login} />
    <Route exact path="/signup" component={Signup} />
    <Route exact path="/dashboard" component={Dashboard} />
    <Route exact path="/dashboard/subscription" component={Subscription} />
    <Route exact path="/dashboard/settings" component={Settings} />
    <Route exact path="/dashboard/myshipments" component={MyShipments} />
    <Route exact path="/dashboard/creditcard" component={CreditCard} />

    <Route
      exact
      path="/dashboard/prepareshipment"
      component={PrepareShipment}
    />
    <Route
      exact
      path="/dashboard/scheduleshipment"
      component={ScheduleShipment}
    />
  </Switch>
);

export default Main;
