import React, { Component } from "react";
import { Form, Col, Button } from "react-bootstrap";
import { Tabs, Tab } from "react-mdl";
import UserStore from "../../../UserStore";
import { Link } from "react-router-dom";
import Interstate from "./interstatep";

import {
  faEdit,
  faMoneyBillWave,
  faCreditCard,
  faIdBadge,
  faSpinner,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import Doc from "../assets/doc.svg";
import Parcel from "../assets/parcel.svg";

class ScheduleShipment extends Component {
  componentWillUnmount() {
    window.removeEventListener("resize", this.resize);
  }

  resize = () => this.forceUpdate();
  constructor(props) {
    super(props);
    this.state = {
      step: 1,
      activeTab: 0,
      date: "",
      time: "",
      plocation: "",
      dlocation: "",
      rname: "",
      rphone: "",
      itemtype: "",
      itemweight: "",
      amount: "",
      payment: "",
      loading: false,
      confirm: "",
    };
  }

  handleform(e) {
    e.preventDefault();
  }

  async componentDidMount() {
    const script = document.createElement("script");
    script.src =
      "https://api.ravepay.co/flwv3-pug/getpaidx/api/flwpbf-inline.js";
    script.async = true;
    document.body.appendChild(script);
    window.addEventListener("resize", this.resize);
  }
  async ScheduleShipment() {
    if (
      this.state.plocation !== "" &&
      this.state.dlocation !== "" &&
      this.state.rname !== "" &&
      this.state.rphone !== "" &&
      this.state.itemtype !== "" &&
      this.state.itemweight !== "" &&
      this.state.amount !== "" &&
      this.state.payment !== ""
    ) {
      try {
        this.setState({ loading: true });
        setTimeout(() => {
          this.setState({ loading: true });
        }, 3000);
        let res = await fetch("/scheduledomestic", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            sessionID: UserStore.sessionID,
            senderphone: UserStore.username,
            sendername: UserStore.name,
            from: UserStore.state,
            to: UserStore.state,
            date: "",
            time: "",
            plocation: this.state.plocation,
            dlocation: this.state.dlocation,
            rname: this.state.rname,
            rphone: this.state.rphone,
            itemtype: this.state.itemtype,
            itemweight: this.state.itemweight,
            amount: this.state.amount,
            payment: this.state.payment,
          }),
        });

        let result = await res.json();
        if (result && result.success) {
          UserStore.amount = this.state.amount;
          this.setState({
            confirm: result.confirm,
          });
          if (this.state.confirm === "yes") {
            return;
          } else {
            return alert(
              "Congratulations, your pickup is scheduled successfully"
            );
          }
        } else if (result && result.success === false) {
          alert(result.msg);
        }
      } catch (e) {
        console.log(e);
      }
    } else {
      return alert("Please fill out your form completely");
    }
  }
  setInputValue(property, e) {
    this.setState({
      [property]: e,
    });

    console.log(([property] = e));
    console.log(this.state);
    console.log(this.state.amount);
  }

  nextStep = () => {
    const { step } = this.state;
    const amounts = (parseFloat(this.state.itemweight) / 0.5) * 500;
    this.setState({
      step: step + 1,
      amount: amounts,
    });
  };
  continue = (e) => {
    e.preventDefault();

    this.nextStep();
  };
  //Go Back
  prevStep = () => {
    const { step } = this.state;
    this.setState({
      step: step - 1,
    });
  };
  back = (e) => {
    e.preventDefault();
    this.prevStep();
  };
  togglePay() {
    if (this.state.confirm === "yes") {
      return (
        <>
          <Form.Row>
            <Form.Group as={Col} controlId="formGridZip">
              <Link to="/dashboard/creditcard">
                <Button
                  className="button_login left1"
                  variant="primary"
                  type="submit"
                >
                  Proceed to pay
                </Button>
              </Link>
            </Form.Group>
          </Form.Row>
        </>
      );
    } else {
      const loading = this.state.loading;
      return (
        <>
          <Form.Row>
            <Form.Group as={Col} controlId="formGridZip">
              <Button
                className="button_login_back"
                variant="primary"
                type="submit"
                onClick={this.back}
              >
                Back
              </Button>
              <Button
                onClick={() => this.ScheduleShipment()}
                className="button_login left1"
                variant="primary"
                disabled={loading}
                type="submit"
              >
                {loading && (
                  <FontAwesomeIcon
                    onClick={this.continue}
                    icon={faSpinner}
                    spin
                  />
                )}
                {loading && <span> Processing</span>}

                {!loading && <span>Request</span>}
              </Button>
            </Form.Group>
            <Form.Group as={Col} controlId="formGridZip"></Form.Group>
          </Form.Row>
        </>
      );
    }
  }
  toggleContent() {
    switch (this.state.step) {
      case 0:
        return (
          <>
            <div className="contentent">
              <Form onSubmit={this.handleform}>
                <p style={{ textAlign: "center" }}>
                  <i>
                    Please enter your convenient Date and Time for the Pickup
                    <br />
                    Time must be between 8am to 8pm{" "}
                  </i>
                </p>
                <br />
                <Form.Row>
                  <Form.Group as={Col} controlId="formGridCity">
                    <Form.Label>Date</Form.Label>
                    <Form.Control
                      className="form-input"
                      type="date"
                      value={this.state.date ? this.state.date : ""}
                      onChange={(e) =>
                        this.setInputValue("date", e.target.value)
                      }
                      required
                    />
                  </Form.Group>

                  <Form.Group as={Col} controlId="formGridZip">
                    <Form.Label>Time</Form.Label>
                    <Form.Control
                      className="form-input"
                      type="time"
                      value={this.state.time ? this.state.time : ""}
                      onChange={(e) =>
                        this.setInputValue("time", e.target.value)
                      }
                    />
                  </Form.Group>
                </Form.Row>
                <Button
                  className="button_login"
                  variant="primary"
                  type="submit"
                  onClick={this.continue}
                >
                  Continue
                </Button>
              </Form>
            </div>
          </>
        );
      case 1:
        return (
          <>
            <div className="contentent">
              <Form onSubmit={this.handleform}>
                <p style={{ textAlign: "center" }}>
                  <i>
                    Enter the Pick and Delivery Location below!
                    <br />
                  </i>
                </p>
                <br />
                <Form.Row>
                  <Form.Group as={Col} controlId="formGridCity">
                    <Form.Label>From</Form.Label>
                    <Form.Control
                      className="form-input"
                      type="text"
                      value={UserStore.state}
                      disabled
                    />
                  </Form.Group>

                  <Form.Group as={Col} controlId="formGridZip">
                    <Form.Label>To</Form.Label>
                    <Form.Control
                      className="form-input"
                      type="text"
                      value={UserStore.state}
                      disabled
                    />
                  </Form.Group>
                </Form.Row>
                <Form.Group controlId="exampleForm.ControlTextarea1">
                  <Form.Label>Pickup Location(Address)</Form.Label>
                  <Form.Control
                    as="textarea"
                    className="form-input-text"
                    rows="3"
                    value={this.state.plocation ? this.state.plocation : ""}
                    onChange={(e) =>
                      this.setInputValue("plocation", e.target.value)
                    }
                  />
                </Form.Group>
                <Form.Group controlId="exampleForm.ControlTextarea1">
                  <Form.Label>Delivery/Receiver`s Location(Address)</Form.Label>
                  <Form.Control
                    className="form-input-text"
                    as="textarea"
                    rows="3"
                    value={this.state.dlocation ? this.state.dlocation : ""}
                    onChange={(e) =>
                      this.setInputValue("dlocation", e.target.value)
                    }
                  />
                </Form.Group>
                <Form.Row>
                  <Form.Group as={Col} controlId="formGridCity">
                    <Form.Label>Receiver`s Name</Form.Label>
                    <Form.Control
                      className="form-input"
                      type="text"
                      value={this.state.rname ? this.state.rname : ""}
                      onChange={(e) =>
                        this.setInputValue("rname", e.target.value)
                      }
                    />
                  </Form.Group>

                  <Form.Group as={Col} controlId="formGridZip">
                    <Form.Label>Receiver`s Tel</Form.Label>
                    <Form.Control
                      className="form-input"
                      type="text"
                      value={this.state.rphone ? this.state.rphone : ""}
                      onChange={(e) =>
                        this.setInputValue("rphone", e.target.value)
                      }
                    />
                  </Form.Group>
                </Form.Row>
                <Form.Row>
                  <Form.Group as={Col} controlId="formGridZip">
                    <Button
                      className="button_login"
                      variant="primary"
                      type="submit"
                      onClick={this.continue}
                    >
                      Continue
                    </Button>
                  </Form.Group>
                  <Form.Group as={Col} controlId="formGridZip"></Form.Group>
                </Form.Row>
              </Form>
            </div>
          </>
        );
      case 2:
        return (
          <>
            <div className="contentent">
              <form>
                <Form onSubmit={this.handleform}>
                  <p style={{ textAlign: "center" }}>
                    <i>
                      Select the item type and enter its weight below!
                      <br />
                    </i>
                  </p>
                  <br />
                  <div className="Shipment-Details">
                    <div className="cards">
                      <div className="radio-item">
                        <input
                          type="radio"
                          id="document"
                          name="itemtype"
                          value={this.state.itemtype ? this.state.itemtype : ""}
                          onChange={(e) =>
                            this.setInputValue(
                              "itemtype",
                              (this.setState.itemtype = e.target.id)
                            )
                          }
                        />

                        <label for="document">
                          <br />
                          <img
                            src={Doc}
                            alt="documents"
                            width="60px"
                            height="60px"
                          />
                          <br />
                          Document(s)
                          <p
                            style={{
                              fontSize: "12px",
                              fontFamily: "Roboto, sans-serif",
                            }}
                          >
                            Documents and printed matter
                          </p>
                          <div className="check"></div>
                        </label>
                      </div>
                    </div>
                    <div className="cards">
                      <div className="radio-item">
                        <input
                          type="radio"
                          id="parcel"
                          name="itemtype"
                          value={this.state.itemtype ? this.state.itemtype : ""}
                          onChange={(e) =>
                            this.setInputValue(
                              "itemtype",
                              (this.setState.itemtype = e.target.id)
                            )
                          }
                        />

                        <label for="parcel">
                          <br />
                          <img
                            src={Parcel}
                            alt="Parcel"
                            width="60px"
                            height="60px"
                          />
                          <br />
                          Parcel
                          <p
                            style={{
                              fontSize: "12px",
                              fontFamily: "Roboto, sans-serif",
                            }}
                          >
                            All other Packages or Objects
                          </p>
                          <div className="check"></div>
                        </label>
                      </div>
                    </div>
                  </div>

                  <Form.Row>
                    <Form.Group as={Col} controlId="formGridCity">
                      <Form.Label>
                        Weight<span style={{ color: "red" }}>*</span>
                      </Form.Label>
                      <Form.Control
                        className="form-input"
                        value={
                          this.state.itemweight ? this.state.itemweight : ""
                        }
                        onChange={(e) =>
                          this.setInputValue("itemweight", e.target.value)
                        }
                        placeholder="0.5"
                        required
                      />
                    </Form.Group>

                    <Form.Group as={Col} controlId="formGridState">
                      <Form.Label>
                        Unit<span style={{ color: "red" }}>*</span>
                      </Form.Label>
                      <Form.Control
                        className="form-input"
                        as="select"
                        defaultValue="Choose..."
                      >
                        <option>Kg</option>
                      </Form.Control>
                    </Form.Group>
                  </Form.Row>

                  <Form.Row>
                    <Form.Group as={Col} controlId="formGridZip">
                      <Button
                        className="button_login_back"
                        variant="primary"
                        type="submit"
                        onClick={this.back}
                      >
                        Back
                      </Button>
                      <Button
                        className="button_login left1"
                        variant="primary"
                        type="submit"
                        onClick={this.continue}
                      >
                        Continue
                      </Button>
                    </Form.Group>
                    <Form.Group as={Col} controlId="formGridZip"></Form.Group>
                  </Form.Row>
                </Form>
              </form>
            </div>
          </>
        );
      case 3:
        const { loading } = this.state;
        return (
          <>
            <div className="contentent">
              <form>
                <Form onSubmit={this.handleform}>
                  <p style={{ textAlign: "center" }}>
                    <i
                      style={{
                        color: "red",
                        fontSize: "20px",
                        fontWeight: "600",
                        fontFamily: "Nunito, sans-serif",
                      }}
                    >
                      Amount:
                    </i>{" "}
                    <p
                      style={{
                        color: "green",
                        fontSize: "30px",
                        fontWeight: "600",
                        fontFamily: "Nunito, sans-serif",
                      }}
                    >
                      {this.state.amount}
                    </p>
                  </p>
                  <p style={{ textAlign: "center" }}>
                    <i>
                      How do you want to pay?
                      <br />
                    </i>
                  </p>
                  <br />
                  <div className="Payment-Type">
                    <div className="Desktop">
                      <div className="cards">
                        <div className="radio-item1">
                          <input
                            type="radio"
                            id="cash"
                            name="payment"
                            value={this.state.payment ? this.state.payment : ""}
                            onChange={(e) =>
                              this.setInputValue(
                                "payment",
                                (this.setState.payment = e.target.id)
                              )
                            }
                          />

                          <label for="cash">
                            <br />
                            <FontAwesomeIcon
                              icon={faMoneyBillWave}
                              style={{
                                display: "inline-block",
                                color: "#800000",
                                fontSize: "30px",
                              }}
                            />
                            <br />
                            In Cash by Shipper or Receiver
                            <div className="check"></div>
                          </label>
                        </div>
                      </div>
                      <div className="cards">
                        <div className="radio-item1">
                          <input
                            type="radio"
                            id="card"
                            name="payment"
                            value={
                              this.setState.payment ? this.state.payment : ""
                            }
                            onChange={(e) =>
                              this.setInputValue(
                                "payment",
                                (this.setState.payment = e.target.id)
                              )
                            }
                          />

                          <label for="card">
                            <br />
                            <FontAwesomeIcon
                              icon={faCreditCard}
                              style={{
                                display: "inline-block",
                                color: "#800000",
                                fontSize: "30px",
                              }}
                            />
                            <br />
                            By Credit Card by the Shipper
                            <div class="check"></div>
                          </label>
                        </div>
                      </div>
                      <div className="cards">
                        <div className="radio-item1">
                          <input
                            type="radio"
                            id="subs"
                            name="payment"
                            value={this.state.subs ? this.state.subs : ""}
                            onChange={(e) =>
                              this.setInputValue(
                                "subs",
                                (this.setState.payment = e.target.id)
                              )
                            }
                          />

                          <label for="subs">
                            <br />
                            <FontAwesomeIcon
                              icon={faIdBadge}
                              style={{
                                display: "inline-block",
                                color: "#800000",
                                fontSize: "30px",
                              }}
                            />
                            <br />
                            Subscription Plans <br /> <br />
                            <div className="check"></div>
                          </label>
                        </div>
                      </div>
                    </div>
                  </div>

                  {this.togglePay()}
                </Form>
              </form>
            </div>
          </>
        );
      default:
        return (
          <>
            <div>
              <div>
                <p>Please refresh your page</p>
              </div>
            </div>
          </>
        );
    }
  }
  toggleCategories() {
    if (this.state.activeTab === 0) {
      return this.toggleContent();
    } else if (this.state.activeTab === 1) {
      return <Interstate />;
    }
  }
  render() {
    return (
      <>
        <div
          style={{
            backgroundColor: "#fff",
            padding: "2em",
            borderRadius: "0.5em",
          }}
        >
          <div className="demo-tabs">
            <div style={{ textAlign: "center" }}>
              <p
                style={{
                  fontFamily: "Nunito, sans-serif",
                  color: "#800000",
                  fontWeight: "900",
                }}
              >
                Online Shipping
              </p>
              <FontAwesomeIcon
                icon={faEdit}
                style={{
                  display: "inline-block",
                  color: "#800000",
                  fontSize: "30px",
                }}
              />
              <p
                style={{
                  fontFamily: "Roboto, sans-serif",
                  color: "#00000",
                  fontSize: "20px",
                }}
              >
                Prepare a Shipment
              </p>
              <p
                style={{
                  fontFamily: "Nunito, sans-serif",
                  color: "#00000",
                  fontSize: "16px",
                }}
              >
                Fill in your details to prepare the shipment label
              </p>
            </div>

            <hr />
            <Tabs
              activeTab={this.state.activeTab}
              onChange={(tabId) => this.setState({ activeTab: tabId })}
              ripple
            >
              <Tab>Domestic</Tab>
              <Tab>Interstate</Tab>
            </Tabs>
            <section>{this.toggleCategories()}</section>
          </div>
        </div>
      </>
    );
  }
}

export default ScheduleShipment;
