import { extendObservable } from "mobx";

/**
 * Userstore
 */
class UserStore {
  constructor() {
    extendObservable(this, {
      loading: true,
      isLoggedIn: false,
      sessionID: "",
      username: "",
      name: "",
      state: "",
      address: "",
      amount: "",
    });
  }
}
export default new UserStore();
