import React, { Component } from "react";
import UserStore from "../UserStore";
import { observer } from "mobx-react";
import { Form, Button, Container, Row, Col } from "react-bootstrap";
import logobox1 from "../images/box11.png";
import { Link } from "react-router-dom";
import { faSpinner } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Dashboard from "./dashboard/dashboard";

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      password: "",
      buttonDisabled: false,
      loading: false,
    };
  }

  setInputValue(property, e) {
    e = e.trim();
    if (e.length > 200) {
      return;
    }
    this.setState({
      [property]: e,
    });
  }
  resetForm() {
    this.setState({
      username: "",
      password: "",
      buttonDisabled: false,
    });
  }
  handleLogin(e) {
    e.preventDefault();
  }
  async doLogin() {
    if (!this.state.username) {
      return;
    }
    if (!this.state.password) {
      return;
    }
    this.setState({
      buttonDisabled: true,
    });
    this.setState({ loading: true });
    setTimeout(() => {
      this.setState({ loading: true });
    }, 3000);
    try {
      let res = await fetch("/login", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          username: this.state.username,
          password: this.state.password,
        }),
      });

      let result = await res.json();
      if (result && result.success) {
        UserStore.isLoggedIn = true;
        UserStore.sessionID = result.sessionID;
        UserStore.username = result.username;
        UserStore.name = result.name;
        UserStore.state = result.state;
        UserStore.address = result.address;
      } else if (result && result.success === false) {
        this.resetForm();
        alert(result.msg);
      }
    } catch (e) {
      console.log(e);
      this.resetForm();
    }
  }
  async componentDidMount() {
    try {
      let res = await fetch("/isLoggedIn", {
        method: "post",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });
      let result = await res.json();
      if (result && result.success) {
        UserStore.loading = false;
        UserStore.isLoggedIn = true;
        UserStore.sessionID = result.sessionID;
        UserStore.username = result.username;
        UserStore.name = result.name;
        UserStore.state = result.state;
        UserStore.address = result.address;
      } else {
        UserStore.loading = false;
        UserStore.isLoggedIn = false;
      }
    } catch (e) {
      UserStore.loading = false;
      UserStore.isLoggedIn = false;
    }
  }
  render() {
    if (UserStore.isLoggedIn === true) {
      return <Dashboard />;
    } else if (UserStore.isLoggedIn === false) {
      const { loading } = this.state;
      return (
        <>
          <div className="remove-grid">
            <Container>
              <Row>
                <Col sm={8}>
                  <div className="logb"></div>
                </Col>
                <Col sm={4}>
                  <div className="log-white">
                    <div
                      style={{
                        position: "relative",
                        textAlign: "center",
                        justifyContent: "center",
                        alignItems: "center",
                        float: "center",
                      }}
                    >
                      <Link className="link" to="/">
                        <img
                          className="logo"
                          src={logobox1}
                          alt="logo"
                          style={{
                            marginLeft: "0",
                            left: "0",
                            textAlign: "center",
                            justifyContent: "center",
                          }}
                        />
                      </Link>
                    </div>

                    <h5
                      style={{
                        fontSize: "1.625rem",
                        lineHeight: " 2.25rem",
                        fontWeight: "700",
                        textAlign: "center",
                        fontFamily: "Roboto, sans-serif",
                      }}
                    >
                      Sign in to Box1
                    </h5>
                    <p
                      style={{
                        fontSize: "1.1rem",

                        fontFamily: "Nunito, sans-serif",
                      }}
                    >
                      Welcome Back
                    </p>
                    <Form onSubmit={this.handleLogin}>
                      <Form.Group controlId="formBasicEmail">
                        <Form.Label>Phone Number</Form.Label>
                        <Form.Control
                          value={this.state.username ? this.state.username : ""}
                          onChange={(e) =>
                            this.setInputValue("username", e.target.value)
                          }
                          type="tel"
                          id="phone"
                          pattern="[0-9]{11}"
                          placeholder="0813 666 8344"
                          className="form-input"
                          required
                        />
                        <Form.Text className="text-muted">
                          We'll never share your phone no with anyone else.
                        </Form.Text>
                      </Form.Group>

                      <Form.Group controlId="formBasicPassword">
                        <Form.Label>Password</Form.Label>{" "}
                        <span
                          style={{
                            position: "relative",
                            textAlign: "right",
                            float: "right",

                            color: "#800000",
                            cursor: "pointer",
                          }}
                        >
                          <Link
                            style={{
                              position: "relative",

                              color: "#800000",
                              cursor: "pointer",
                            }}
                          >
                            Forgot Password?
                          </Link>
                        </span>
                        <Form.Control
                          value={this.state.password ? this.state.password : ""}
                          onChange={(e) =>
                            this.setInputValue("password", e.target.value)
                          }
                          type="password"
                          className="form-input"
                          placeholder="Password"
                          required
                        />
                      </Form.Group>
                      <Form.Group controlId="formBasicCheckbox">
                        <Form.Check type="checkbox" label="Stay Logged in" />
                      </Form.Group>
                      <Button
                        disabled={this.state.buttonDisabled + loading}
                        onClick={() => this.doLogin()}
                        variant="primary"
                        type="submit"
                      >
                        {loading && (
                          <FontAwesomeIcon
                            onClick={this.continue}
                            icon={faSpinner}
                            spin
                          />
                        )}
                        {loading && <span> Logging in</span>}

                        {!loading && <span>Log in</span>}
                      </Button>
                      <br />

                      <br />

                      <p
                        style={{
                          fontSize: "1.1rem",
                          fontFamily: "Nunito, sans-serif",
                        }}
                      >
                        {" "}
                        Don`t have an account yet?{" "}
                        <Link
                          className="link"
                          to="/signup"
                          style={{
                            position: "relative",

                            color: "#800000",
                            fontWeight: "600",
                            cursor: "pointer",
                          }}
                        >
                          Create an Account
                        </Link>
                      </p>
                    </Form>
                  </div>
                </Col>
              </Row>
            </Container>
          </div>
        </>
      );
    }
  }
}
export default observer(Login);
