import React, { Component } from "react";
import { StyleSheet, css } from "aphrodite";
import { Column, Row } from "simple-flexbox";
import SidebarComponent from "../sidebar/SidebarComponent";
import HeaderComponent from "../header/HeaderComponent";

import { makeStyles } from "@material-ui/core/styles";

import UserStore from "../../../UserStore";

const styles = StyleSheet.create({
  container: {
    height: "100%",
    width: "100% !important",
    minHeight: "100vh",
    "@media (max-width: 768px)": {
      width: "100%",
    },
  },
  content: {
    marginTop: 54,
  },
  mainBlock: {
    backgroundColor: "#F7F8FC",
    padding: 30,
  },
});
class CreditCard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedItem: "Card",
      number: "",
      name: "",
      expiry: "",
      cardexpmonth: "",
      cardexpyear: "",
      cvc: "",
      issuer: "",
      focused: "",
      formData: null,
      loading: false,
      available: "",
      results: [],
    };
  }
  async DeleteCard() {
    try {
      let res = await fetch("/deletecard", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          sessionID: UserStore.sessionID,
        }),
      });
      let result = await res.json();
      if (result && result.success) {
        return alert("Your Card has been deleted successfully");
      } else if (result && result.success === false) {
        alert(result.msg);
      }
    } catch (e) {
      console.log(e);
    }
  }
  async componentDidMount() {
    window.addEventListener("resize", this.resize);
    const script = document.createElement("script");
    script.src =
      "https://api.ravepay.co/flwv3-pug/getpaidx/api/flwpbf-inline.js";
    script.async = true;
    document.body.appendChild(script);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.resize);
  }

  resize = () => this.forceUpdate();

  render() {
    const { selectedItem } = this.state;

    return (
      <>
        <div className="dashboard">
          <Row className={css(styles.container)}>
            <SidebarComponent
              selectedItem={selectedItem}
              onChange={(selectedItem) => this.setState({ selectedItem })}
            />
            <Column flexGrow={1} className={css(styles.mainBlock)}>
              <HeaderComponent title={selectedItem} />
              <div className={css(styles.content)}>
                <div
                  style={{
                    backgroundColor: "#fff",
                    padding: "2em",
                    borderRadius: "0.5em",
                  }}
                >
                  <div className="demo-tabs">
                    <div
                      className="contentent"
                      style={{ width: "100% !important" }}
                    >
                      <form>
                        <a
                          class="flwpug_getpaid"
                          data-PBFPubKey="FLWPUBK-4c45fc2e72a23d406b83f7439f70e3d6-X"
                          data-txref="rave-123456"
                          data-amount={UserStore.amount}
                          data-customer_email={UserStore.username + "@box1.ng"}
                          data-currency="NGN"
                          data-pay_button_text="Proceed to Pay Now"
                          data-country="NG"
                          data-redirect_url="/dashboard/thanks"
                        ></a>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </Column>
          </Row>
        </div>
      </>
    );
  }
}
export default CreditCard;
