import React, { Component } from "react";
import { Column, Row } from "simple-flexbox";
import { StyleSheet, css } from "aphrodite";
import UserStore from "../../UserStore";
import SidebarComponent from "./sidebar/SidebarComponent";
import HeaderComponent from "./header/HeaderComponent";
import ContentComponent from "./content/ContentComponent";
import PrepareShipment from "./content/PrepareShipment";
import ScheduleShipment from "./content/ScheduleShipment";
import MyShipments from "./content/myshipments";
import Settings from "./content/settings";
import Subscription from "./content/Subscription";

const styles = StyleSheet.create({
  container: {
    height: "100%",
    width: "100% !important",
    minHeight: "100vh",
    "@media (max-width: 768px)": {
      width: "100%",
    },
  },
  content: {
    marginTop: 54,
  },
  mainBlock: {
    backgroundColor: "#F7F8FC",
    padding: 30,
  },
});

class Dashboard extends Component {
  async componentDidMount() {
    window.addEventListener("resize", this.resize);
  }
  state = { selectedItem: "Overview" };

  componentWillUnmount() {
    window.removeEventListener("resize", this.resize);
  }

  resize = () => this.forceUpdate();
  render() {
    const { selectedItem } = this.state;
    if (selectedItem === "Overview") {
      return (
        <>
          <div className="dashboard">
            <Row className={css(styles.container)}>
              <SidebarComponent
                selectedItem={selectedItem}
                onChange={(selectedItem) => this.setState({ selectedItem })}
              />
              <Column flexGrow={1} className={css(styles.mainBlock)}>
                <HeaderComponent title={selectedItem} />
                <div className={css(styles.content)}>
                  <ContentComponent />
                </div>
              </Column>
            </Row>
          </div>
        </>
      );
    } else if (selectedItem === "Shipment") {
      return (
        <>
          <div className="dashboard">
            <Row className={css(styles.container)}>
              <SidebarComponent
                selectedItem={selectedItem}
                onChange={(selectedItem) => this.setState({ selectedItem })}
              />
              <Column flexGrow={1} className={css(styles.mainBlock)}>
                <HeaderComponent title={selectedItem} />
                <div className={css(styles.content)}>
                  <PrepareShipment />
                </div>
              </Column>
            </Row>
          </div>
        </>
      );
    } else if (selectedItem === "Pickup") {
      return (
        <>
          <div className="dashboard">
            <Row className={css(styles.container)}>
              <SidebarComponent
                selectedItem={selectedItem}
                onChange={(selectedItem) => this.setState({ selectedItem })}
              />
              <Column flexGrow={1} className={css(styles.mainBlock)}>
                <HeaderComponent title={selectedItem} />
                <div className={css(styles.content)}>
                  <ScheduleShipment />
                </div>
              </Column>
            </Row>
          </div>
        </>
      );
    } else if (selectedItem === "Myshipments") {
      return (
        <>
          <div className="dashboard">
            <Row className={css(styles.container)}>
              <SidebarComponent
                selectedItem={selectedItem}
                onChange={(selectedItem) => this.setState({ selectedItem })}
              />
              <Column flexGrow={1} className={css(styles.mainBlock)}>
                <HeaderComponent title={selectedItem} />
                <div className={css(styles.content)}>
                  <MyShipments />
                </div>
              </Column>
            </Row>
          </div>
        </>
      );
    } else if (selectedItem === "Settings") {
      return (
        <>
          <div className="dashboard">
            <Row className={css(styles.container)}>
              <SidebarComponent
                selectedItem={selectedItem}
                onChange={(selectedItem) => this.setState({ selectedItem })}
              />
              <Column flexGrow={1} className={css(styles.mainBlock)}>
                <HeaderComponent title={selectedItem} />
                <div className={css(styles.content)}>
                  <Settings />
                </div>
              </Column>
            </Row>
          </div>
        </>
      );
    } else if (selectedItem === "Subscription") {
      return (
        <>
          <div className="dashboard">
            <Row className={css(styles.container)}>
              <SidebarComponent
                selectedItem={selectedItem}
                onChange={(selectedItem) => this.setState({ selectedItem })}
              />
              <Column flexGrow={1} className={css(styles.mainBlock)}>
                <HeaderComponent title={selectedItem} />
                <div className={css(styles.content)}>
                  <Subscription />
                </div>
              </Column>
            </Row>
          </div>
        </>
      );
    }
  }
}
export default Dashboard;
