import React, { Component } from "react";
import { Tabs, Tab } from "react-mdl";
import UserStore from "../../../UserStore";
import { Form, Col, Button } from "react-bootstrap";
import { faSpinner } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

class Settings extends Component {
  async componentDidMount() {
    window.addEventListener("resize", this.resize);
    try {
      let res = await fetch("/isLoggedIn", {
        method: "post",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });

      let result = await res.json();
      if (result && result.success) {
        UserStore.loading = false;
        UserStore.isLoggedIn = true;
        UserStore.username = result.username;
      } else {
        UserStore.loading = false;
        UserStore.isLoggedIn = false;
      }
    } catch (e) {
      UserStore.loading = false;
      UserStore.isLoggedIn = false;
    }
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.resize);
  }

  resize = () => this.forceUpdate();
  constructor(props) {
    super(props);

    this.state = {
      activeTab: 0,
      name: UserStore.name,
      rstate: UserStore.state,
      address: UserStore.address,
      oldpassword: "",
      newpassword: "",
      cpassword: "",
      loading: false,
      loader: false,
    };
  }
  handleform(e) {
    e.preventDefault();
  }

  setInputValue(property, e) {
    this.setState({
      [property]: e,
    });
  }
  async UpdatePersonal() {
    if (!this.state.name) {
      return;
    }
    if (!this.state.rstate) {
      return;
    }
    if (!this.state.address) {
      return;
    }
    this.setState({ loading: true });
    setTimeout(() => {
      this.setState({ loading: true });
    }, 3000);

    try {
      let res = await fetch("/updateinfo", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          sessionID: UserStore.sessionID,
          name: this.state.name,
          state: this.state.rstate,
          address: this.state.address,
        }),
      });

      let result = await res.json();
      if (result && result.success) {
        UserStore.name = result.name;
        UserStore.state = result.state;
        UserStore.address = result.address;
        return alert("Information Updated Successfully");
      } else if (result && result.success === false) {
        alert("An error occur, please try later");
      }
    } catch (e) {
      console.log(e);
    }
  }

  async UpdatePassword() {
    const password = UserStore.password;
    if (this.state.oldpassword === password) {
      if (this.state.newpassword === this.state.cpassword) {
        this.setState({ loader: true });
        setTimeout(() => {
          this.setState({ loader: true });
        }, 3000);
        try {
          let res = await fetch("/updatepassword", {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify({
              sessionID: UserStore.sessionID,
              password: this.state.newpassword,
            }),
          });

          let result = await res.json();
          if (result && result.success) {
            return alert("Password Changed Successfully");
          } else if (result && result.success === false) {
            alert("An error occur, please try later");
          }
        } catch (e) {
          console.log(e);
        }
      } else {
        return alert("New and Confirm new Password must match");
      }
    } else {
      return alert("Old and New Password must match");
    }
  }
  toggleCategories() {
    if (this.state.activeTab === 0) {
      const { loading } = this.state;
      return (
        <>
          <div className="contentent">
            <div className="personal-info">
              <h5
                className="headline"
                style={{
                  fontFamily: "Roboto, sans-serif",

                  fontSize: "16px",
                }}
              >
                Personal Information:
                <hr
                  style={{
                    width: "8em",
                    height: "0.3em",
                    borderRadius: "1em",
                    backgroundColor: "#800000",
                    marginTop: "0em",
                  }}
                />
              </h5>
              <Form onSubmit={this.handleform}>
                <Form.Row>
                  <Form.Group as={Col} controlId="formGridCity">
                    <Form.Label>Full Name</Form.Label>
                    <Form.Control
                      className="form-input"
                      type="text"
                      placeholder={UserStore.name}
                      value={this.state.name ? this.state.name : ""}
                      onChange={(e) =>
                        this.setInputValue("name", e.target.value)
                      }
                    />
                  </Form.Group>
                  <Form.Group as={Col} controlId="exampleForm.ControlSelect1">
                    <Form.Label>To</Form.Label>
                    <Form.Control
                      className="form-input"
                      name="tostate"
                      value={
                        this.state.rstate ? this.state.rstate : UserStore.state
                      }
                      onChange={(e) =>
                        this.setInputValue("rstate", e.target.value)
                      }
                      as="select"
                      required
                    >
                      <option>{UserStore.state}</option>
                      <option>Abuja</option>
                      <option>Abekuta</option>
                      <option>Abia</option>
                      <option>Adamawa</option>
                      <option>Bauchi</option>
                      <option>Borno</option>
                      <option>Enugu</option>
                      <option>Gombe</option>
                      <option>Ilorin</option>
                      <option>Kano</option>
                      <option>kaduna</option>
                      <option>katsina</option>
                      <option>Lagos</option>
                      <option>Onitsha</option>
                      <option>Sokoto</option>
                      <option>Yobe</option>
                    </Form.Control>
                  </Form.Group>
                </Form.Row>
                <Form.Group controlId="exampleForm.ControlTextarea1">
                  <Form.Label>Residential Address</Form.Label>
                  <Form.Control
                    className="form-input-text"
                    as="textarea"
                    rows="3"
                    placeholder={UserStore.address}
                    value={this.state.address ? this.state.address : ""}
                    onChange={(e) =>
                      this.setInputValue("address", e.target.value)
                    }
                  />
                </Form.Group>
                <Button
                  onClick={() => this.UpdatePersonal()}
                  className="button_login_back"
                  variant="primary"
                  type="submit"
                  disabled={loading}
                >
                  {loading && (
                    <FontAwesomeIcon
                      onClick={this.continue}
                      icon={faSpinner}
                      spin
                    />
                  )}
                  {loading && <span> Saving Data</span>}

                  {!loading && <span>Save Changes</span>}
                </Button>
              </Form>
            </div>
          </div>
        </>
      );
    } else if (this.state.activeTab === 1) {
      const { loader } = this.state;
      return (
        <>
          <div className="contentent">
            <div className="personal-info">
              <h5
                className="headline"
                style={{
                  fontFamily: "Roboto, sans-serif",

                  fontSize: "16px",
                }}
              >
                Change Password:
                <hr
                  style={{
                    width: "8em",
                    height: "0.3em",
                    borderRadius: "1em",
                    backgroundColor: "#800000",
                    marginTop: "0em",
                  }}
                />
              </h5>
              <Form.Row>
                <Form.Group
                  controlId="formGridCity"
                  style={{ marginLeft: "0.5em" }}
                >
                  <Form.Label>Cur. Password</Form.Label>
                  <Form.Control
                    className="form-input"
                    type="password"
                    value={this.state.oldpassword ? this.state.oldpassword : ""}
                    onChange={(e) =>
                      this.setInputValue("oldpassword", e.target.value)
                    }
                    placeholder="Current Password"
                  />
                </Form.Group>
              </Form.Row>

              <Form.Row>
                <Form.Group
                  controlId="formGridCity"
                  style={{ marginLeft: "0.5em" }}
                >
                  <Form.Label>New. Password</Form.Label>
                  <Form.Control
                    className="form-input"
                    type="password"
                    value={this.state.newpassword ? this.state.newpassword : ""}
                    onChange={(e) =>
                      this.setInputValue("newpassword", e.target.value)
                    }
                    placeholder="New Password"
                  />
                </Form.Group>
              </Form.Row>
              <Form.Row>
                <Form.Group
                  controlId="formGridZip"
                  style={{ marginLeft: "0.5em" }}
                >
                  <Form.Label>Con. Password</Form.Label>
                  <Form.Control
                    className="form-input"
                    value={this.state.cpassword ? this.state.cpassword : ""}
                    onChange={(e) =>
                      this.setInputValue("cpassword", e.target.value)
                    }
                    type="password"
                    placeholder="Confirm New Password"
                  />
                </Form.Group>
              </Form.Row>

              <Button
                className="button_login_back"
                variant="primary"
                type="submit"
                disabled={loader}
                onClick={() => this.UpdatePassword()}
              >
                {loader && (
                  <FontAwesomeIcon
                    onClick={this.continue}
                    icon={faSpinner}
                    spin
                  />
                )}
                {loader && <span> Changing Data</span>}

                {!loader && <span>Change Password</span>}
              </Button>
            </div>
          </div>
        </>
      );
    }
  }
  render() {
    return (
      <>
        <div
          style={{
            backgroundColor: "#fff",
            padding: "2em",
            borderRadius: "0.5em",
          }}
        >
          <div className="demo-tabs">
            <Tabs
              activeTab={this.state.activeTab}
              onChange={(tabId) => this.setState({ activeTab: tabId })}
              ripple
            >
              <Tab>Personal Info</Tab>
              <Tab>Change Password</Tab>
            </Tabs>
            <section>{this.toggleCategories()}</section>
          </div>
        </div>
      </>
    );
  }
}
export default Settings;
