import React, { Component } from "react";
import photo1 from "../images/interstate.jpg";
import photo2 from "../images/intracity.jpg";
import photo3 from "../images/interregion.jpeg";
import photo4 from "../images/document.png";
import photo5 from "../images/doordelivery.jpg";
import photo6 from "../images/interregion.jpeg";
import {
  faArrowCircleLeft,
  faArrowCircleRight,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Grid, Cell } from "react-mdl";

class Solution extends Component {
  constructor(props) {
    super(props);
    this.state = {
      step: 1,
    };
  }
  nextStep = () => {
    const { step } = this.state;
    this.setState({
      step: step + 1,
    });
  };
  continue = (e) => {
    e.preventDefault();
    this.nextStep();
  };
  //Go Back
  prevStep = () => {
    const { step } = this.state;
    this.setState({
      step: step - 1,
    });
  };
  back = (e) => {
    e.preventDefault();
    this.prevStep();
  };
  render() {
    const { step } = this.state;
    switch (step) {
      case 1:
        return (
          <>
            <div style={{ width: "80%", margin: "auto" }}>
              <Grid className="demo-grid-ruler">
                <Cell col={6}>
                  <div className="photo-left">
                    <img src={photo2} alt="solutions" data-radium="true" />
                  </div>
                </Cell>
                <Cell col={6}>
                  <div className="content-right">
                    <h4>Our Solutions.</h4>
                    <h5>Intra-City Delivery</h5>
                    <p>
                      A superior express delivery service built for small
                      businesses across the country.{" "}
                    </p>
                  </div>
                  <div className="rights">
                    <button onClick={this.continue} className="ar-right">
                      <FontAwesomeIcon
                        onClick={this.continue}
                        icon={faArrowCircleRight}
                      />
                    </button>
                  </div>
                </Cell>
              </Grid>
            </div>
          </>
        );
      case 2:
        return (
          <>
            <div style={{ width: "80%", margin: "auto" }}>
              <Grid className="demo-grid-ruler">
                <Cell col={6}>
                  <div className="photo-left">
                    <div className="lefts">
                      <button onClick={this.back} className="ar-left">
                        <FontAwesomeIcon
                          onClick={this.back}
                          icon={faArrowCircleLeft}
                        />
                      </button>
                    </div>
                    <img src={photo1} alt="solutions" data-radium="true" />
                  </div>
                </Cell>
                <Cell col={6}>
                  <div className="content-right">
                    <h4>Our Solutions.</h4>
                    <h5>Inter-State Delivery</h5>
                    <p>
                      A superior express delivery service built for small
                      businesses across the country.{" "}
                    </p>
                  </div>
                  <div className="rights">
                    <button onClick={this.continue} className="ar-right">
                      <FontAwesomeIcon
                        onClick={this.continue}
                        icon={faArrowCircleRight}
                      />
                    </button>
                  </div>
                </Cell>
              </Grid>
            </div>
          </>
        );
      case 3:
        return (
          <>
            <div style={{ width: "80%", margin: "auto" }}>
              <Grid className="demo-grid-ruler">
                <Cell col={6}>
                  <div className="photo-left">
                    <div className="lefts">
                      <button onClick={this.back} className="ar-left">
                        <FontAwesomeIcon
                          onClick={this.back}
                          icon={faArrowCircleLeft}
                        />
                      </button>
                    </div>
                    <img src={photo3} alt="solutions" data-radium="true" />
                  </div>
                </Cell>
                <Cell col={6}>
                  <div className="content-right">
                    <h4>Our Solutions.</h4>
                    <h5>Inter-Region Delivery</h5>
                    <p>
                      A superior express delivery service built for small
                      businesses across the country.{" "}
                    </p>
                  </div>
                  <div className="rights">
                    <button onClick={this.continue} className="ar-right">
                      <FontAwesomeIcon
                        onClick={this.continue}
                        icon={faArrowCircleRight}
                      />
                    </button>
                  </div>
                </Cell>
              </Grid>
            </div>
          </>
        );
      case 4:
        return (
          <>
            <div style={{ width: "80%", margin: "auto" }}>
              <Grid className="demo-grid-ruler">
                <Cell col={6}>
                  <div className="photo-left">
                    <div className="lefts">
                      <button onClick={this.back} className="ar-left">
                        <FontAwesomeIcon
                          onClick={this.back}
                          icon={faArrowCircleLeft}
                        />
                      </button>
                    </div>
                    <img src={photo4} alt="solutions" data-radium="true" />
                  </div>
                </Cell>
                <Cell col={6}>
                  <div className="content-right">
                    <h4>Our Solutions.</h4>
                    <h5>Safe Document Delivery</h5>
                    <p>
                      A superior express delivery service built for small
                      businesses across the country.{" "}
                    </p>
                  </div>
                  <div className="rights">
                    <button onClick={this.continue} className="ar-right">
                      <FontAwesomeIcon
                        onClick={this.continue}
                        icon={faArrowCircleRight}
                      />
                    </button>
                  </div>
                </Cell>
              </Grid>
            </div>
          </>
        );
      case 5:
        return (
          <>
            <div style={{ width: "80%", margin: "auto" }}>
              <Grid className="demo-grid-ruler">
                <Cell col={6}>
                  <div className="photo-left">
                    <div className="lefts">
                      <button onClick={this.back} className="ar-left">
                        <FontAwesomeIcon
                          onClick={this.back}
                          icon={faArrowCircleLeft}
                        />
                      </button>
                    </div>
                    <img src={photo5} alt="solutions" data-radium="true" />
                  </div>
                </Cell>
                <Cell col={6}>
                  <div className="content-right">
                    <h4>Our Solutions.</h4>
                    <h5>Door to Door Delivery</h5>
                    <p>
                      A superior express delivery service built for small
                      businesses across the country.{" "}
                    </p>
                  </div>
                  <div className="rights">
                    <button onClick={this.continue} className="ar-right">
                      <FontAwesomeIcon
                        onClick={this.continue}
                        icon={faArrowCircleRight}
                      />
                    </button>
                  </div>
                </Cell>
              </Grid>
            </div>
          </>
        );
      case 6:
        return (
          <>
            <div style={{ width: "80%", margin: "auto" }}>
              <Grid className="demo-grid-ruler">
                <Cell col={6}>
                  <div className="photo-left">
                    <div className="lefts">
                      <button onClick={this.back} className="ar-left">
                        <FontAwesomeIcon
                          onClick={this.back}
                          icon={faArrowCircleLeft}
                        />
                      </button>
                    </div>
                    <img src={photo6} alt="solutions" data-radium="true" />
                  </div>
                </Cell>
                <Cell col={6}>
                  <div className="content-right">
                    <h4>Our Solutions.</h4>
                    <h5>Inter-Regional Delivery</h5>
                    <p>
                      A superior express delivery service built for small
                      businesses across the country.{" "}
                    </p>
                  </div>
                </Cell>
              </Grid>
            </div>
          </>
        );

      default:
        return <h1>Please Refresh your page</h1>;
    }
  }
}
export default Solution;
